// Content Modal




// Options

$bm-components-rkw-footer : true;

$bm-components-rkw-footer-namespace : 'rkw-footer' !default;

// Helper Local Variables

$bm-components-rkw-footer-namespace-var : $iota-global-components-namespace + $bm-components-rkw-footer-namespace;

$rkw-footer-selector: $bm-components-rkw-footer-namespace-var;

.#{$rkw-footer-selector} {
  padding-top: 3rem;

  &--home {
    position: fixed;
    width: 119.6rem;
    bottom: 0;
    left: 50%;
    transform: translatex(-50%);
  }

}

.#{$rkw-footer-selector}__copyright {
  text-align: center;
  font-size: 1.8rem;
  line-height: 2.4rem;
  &--home {
    font-size: 1.4rem;
    line-height: 2.1rem;
    color: #575757;
  }
}

.#{$rkw-footer-selector}__link {
  color: #ffffff;
  text-decoration: underline;
  font-size: 1.8rem;
  line-height: 2.4rem;
  &::after {
    content: "|";
    color: #ffffff;
    padding-left: .4rem;
    padding-right: .4rem;
    text-decoration: none;
  }
  &:last-child {
    &::after {
      content: "";
    }
  }
  &--home {
    color: #575757!important;
    font-size: 1.4rem;
    line-height: 2.1rem;
    &::after {
      color: #575757;
    }
  }
}




