// Content Modal




// Options

$bm-components-rkw-bookmark-modal : true;

$bm-components-rkw-bookmark-modal-namespace : 'rkw-bookmark-modal' !default;

// Helper Local Variables

$bm-components-rkw-bookmark-modal-namespace-var-nav : $iota-global-components-namespace + $bm-components-rkw-bookmark-modal-namespace;

$rkw-bookmark-modal-selector: $bm-components-rkw-bookmark-modal-namespace-var-nav;

.#{$rkw-bookmark-modal-selector} {
  position: fixed;
  top: 0;
  right: 0;
  width: 0;
  height: 0;
  margin-left: 0;
  z-index: 2000;
  opacity: 0;
  overflow: hidden;
  border: none;
  transition: width 0.5s ease, height 0.5s ease, opacity 0.5s ease;
  background: #f3f3f3;
  // @extend .u-bg-rkw-grey;
}

.#{$rkw-bookmark-modal-selector}__wrapper {
  padding-top: 4.5rem;
}

.#{$rkw-bookmark-modal-selector}__close {
  position: absolute;
  width: 5rem;
  height: 5rem;
  top: 0;
  right: -3rem;
  transform: translatex(100%);
  background: transparent;
  &::after {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0) rotate(180deg);
    font: normal normal normal 3.5rem/5rem 'rkw-digikit';
    content: "\e914";
    color: #575757;
  }


  &--save-list {
    right: 1rem!important;
    top: 1rem!important;
    transform: translateX(0);
  }

}

.#{$rkw-bookmark-modal-selector}--open {
  width: 100%!important;
  height: 100%!important;
  opacity: 1!important;
  overflow: auto!important;
}

.#{$rkw-bookmark-modal-selector}__header {
  padding-top: 18rem;
  margin-bottom: 6rem;
  border-bottom: .4rem solid #575757;
  position: relative;
}

.#{$rkw-bookmark-modal-selector}__share-container {
  display: inline-block;
  position: absolute;
  right: 0;
  bottom: 0;
  height: 5rem;

  &--bottom {
    line-height: 5rem;
    top: auto!important;
    bottom: -1rem!important;
    transform: translatey(100%);

    .#{$rkw-bookmark-modal-selector}__share-item {
      margin-left: .5rem;
    }

  }

  .#{$rkw-bookmark-modal-selector}__share-label {
    vertical-align: top;
    display: inline-block;
    line-height: 5rem;
    font-weight: 700;
    color: rgba(0,72,98,1);
  }

}

.#{$rkw-bookmark-modal-selector}__share-item {
  position: relative;
  display: inline-block;
  width: 4rem;
  height: 5rem;
  text-align: right;
  cursor: pointer;
  color: #575757;
  &::after {
    font: normal normal normal 3rem/5rem 'rkw-digikit' !important;
  }
  &--xing {
    &::after {
      content: "\f169";
      color: rgba(0,72,98,1);
      font-size: 4rem!important;
    }
  }
  &--facebook {
    &::after {
      content: "\e906";
      color: rgba(0,72,98,1);
      font-size: 4rem!important;
    }
  }
  &--twitter {
    &::after {
      content: "\e912";
      color: rgba(0,72,98,1);
      font-size: 4rem!important;
    }
  }
  &--linkedin {
    &::after {
      content: "\e90d";
      color: rgba(0,72,98,1);
      font-size: 4rem!important;
    }
  }
  &--email {
    &::after {
      content: "\e905";
      color: rgba(0,72,98,1);
      font-size: 4rem!important;
    }
  }

  &--link {
    &::after {
      content: "\e90c";
      color: #e74414;
    }
  }

  &--favorite {
    &::after {
      content: "\e902";
      color: #e74414;
    }
  }

  &--disk {
    &::after {
      background: url($path-assets-images + 'disk.svg') no-repeat center center;
      background-size: contain;
      content: " ";
      position: absolute;
      top: 50%;
      left: 50%;
      width: 80%;
      height: 80%;
      transform: translate3d(-50%, -50%,0);
    }
  }

  &--copied {
    color: green!important;
  }

}

.#{$rkw-bookmark-modal-selector}__headline {
  font-size: 2.6rem;
  line-height: 3.8rem;
  font-family: "ff-tisa-sans-web-pro", sans-serif;
  font-weight: 600;
  text-transform: uppercase;
  color: #575757;
}

.#{$rkw-bookmark-modal-selector}__group-header {
  font-size: 2.8rem;
  line-height: 3.8rem;
  font-family: "ff-tisa-sans-web-pro", sans-serif;
  font-weight: 400;
  margin-bottom: 2.5rem;

  &--color-1 {
    @extend .u-color-rkw-color-1;
  }
  &--color-2 {
    @extend .u-color-rkw-color-2;
  }
  &--color-3 {
    @extend .u-color-rkw-color-3;
  }
  &--color-4 {
    @extend .u-color-rkw-color-4;
  }

}

.#{$rkw-bookmark-modal-selector}__bookmark {
  background: #e1e1e1;
  position: relative;
  width: 100%;
  padding-bottom: 100%;
  margin-bottom: 2.5rem;
  box-shadow: 0.2rem 0.2rem 0.7rem #aaaaaa;
  cursor: pointer;

  &::after {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.7);
    z-index: 100;
    content: " ";
  }
  /*
  &--color-1 {
    &::after {
      background-color: rgba(171,130,62,0.75);
    }
  }

  &--color-2 {
    &::after {
      background-color: rgba(58,49,65,0.75);
    }
  }

  &--color-3 {
    &::after {
      background-color: rgba(98,99,51,0.75);
    }
  }

  &--color-4 {
    &::after {
      background-color: rgba(0,72,98,0.75);
    }
  }
  */
  &:hover {
    .#{$rkw-bookmark-modal-selector}__bookmark-remove {
      opacity: 0.6;
    }

    .#{$rkw-bookmark-modal-selector}__bookmark-text {
      transform: translate3d(-50%, -50%, 0) scale(1.05);
    }
  }

}

.#{$rkw-bookmark-modal-selector}__bookmark-remove {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  display: inline-block;
  z-index: 400;
  width: 3rem;
  height: 3rem;
  background-color: #e97756;
  border-radius: 3rem;
  border: .3rem solid #e97756;
  color: #ffffff;
  cursor: pointer;
  transition: opacity 0.2s ease;
  opacity: 0;
  &::after {
    position: absolute;
    top: 50%;
    left: 50%;
    font: normal normal normal 2.2rem/3rem 'rkw-digikit' !important;
    content: "\e903";
    transform: translate3d(-50%, -50%, 0);
  }
  &:hover {
    opacity: 1!important;
  }
}

.#{$rkw-bookmark-modal-selector}__bookmark-text {
  width: 100%;
  height: auto;
  text-align: center;
  position: absolute;
  padding: 1.5rem;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0) scale(1);
  z-index: 200;
  color: #ffffff;
  font-size: 1.8rem;
  line-height: 2.4rem;
  transition: transform 0.2s ease;
}


.#{$rkw-bookmark-modal-selector}__save-list {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 2000;
  background-color: rgba(0,0,0,.8);
}

.#{$rkw-bookmark-modal-selector}__save-list-container {

  position: absolute;
  top: 50%;
  left: 50%;
  width: 59.8rem;
  height: auto;
  min-height: 15rem;
  transform: translate3d(-50%, -50%, 0);
  background: #ffffff;
  padding: 5rem;

}

.#{$rkw-bookmark-modal-selector}__save-list-header {
  font-size: 2.8rem;
  line-height: 3.8rem;
  font-family: "ff-tisa-sans-web-pro", sans-serif;
  font-weight: 400;
  margin-bottom: 2.5rem;
}

.#{$rkw-bookmark-modal-selector}__save-list-body {
  margin-bottom: 1.5rem;
  font-size: 1.4rem;
  line-height: 2.1rem;
}

.#{$rkw-bookmark-modal-selector}__save-list-url {
  padding: 1rem;
  border: 0.1rem solid #575757;
  font-size: 1.6rem;
  line-height: 2.1rem;
  margin-bottom: 2.5rem;
}




