// Content Modal




// Options

$bm-components-rkw-modal : true;

$bm-components-rkw-modal-namespace : 'rkw-modal' !default;

$bm-components-rkw-modal-color-1: #b49058;
$bm-components-rkw-modal-color-2: #544d5a;
$bm-components-rkw-modal-color-3: #626334;
$bm-components-rkw-modal-color-4: #265f76;

// Helper Local Variables

$bm-components-rkw-modal-namespace-var-nav : $iota-global-components-namespace + $bm-components-rkw-modal-namespace;

$rkw-modal-selector: $bm-components-rkw-modal-namespace-var-nav;

.#{$rkw-modal-selector} {
  position: absolute;
  top: 50%;
  left: 50%;
  right: auto;
  width: 0;
  height: 0;
  margin-left: 0;
  transform: translate3d(-50%,-50%,0);
  transform-origin: center center;
  background: transparent;
  z-index: 1000;
  opacity: 0;

  border: none;
  transition: transform 0.5s ease, width 0.5s ease, height 0.5s ease, margin-left 0.5s ease, opacity 0.5s ease, position 0.5s ease;
}

.#{$rkw-modal-selector}--open-left {
  width: 50%!important;
  height: 100%!important;
  transform: translate3d(-100%,-50%,0)!important;
  margin-left: -1.5rem!important;
  opacity: 1!important;
  border-top: 3rem solid transparent!important;
  border-bottom: 3rem solid transparent!important;

  .c-rkw-content__big-wrap {
    display: none;
  }

}

.#{$rkw-modal-selector}--open-right {
  width: 50%!important;
  height: 100%!important;
  transform: translate3d(0,-50%,0)!important;
  margin-left: 1.5rem!important;
  opacity: 1!important;
  border-top: 3rem solid transparent!important;
  border-bottom: 3rem solid transparent!important;

  .c-rkw-content__big-wrap {
    display: none;
  }

}

.#{$rkw-modal-selector}--open-big {
  position: fixed;
  width: 100%!important;
  height: 100%!important;
  transform: translate3d(-50%,-50%,0)!important;
  margin-left: 0!important;
  opacity: 1!important;
  border: none!important;
  overflow: auto;

  .bookmarks {
    color: #ffffff;
    right: 0rem;
    top: 1rem;
    background: transparent;
    &::after {
      color: #ffffff;
      font: normal normal normal 4rem/4rem 'rkw-digikit' !important;
      content: "\e907";
    }
  }

  .c-rkw-tutorial {
    top: 1.2rem;
    right: 6rem;
    color: #ffffff;
    &::after {
      color: #ffffff;
      font: normal normal normal 3rem/4rem 'rkw-digikit' !important;
      top: 50%;
    }
  }

  .logo {
    background: url($path-assets-images + 'rkw-logo-alternative.svg') no-repeat;
  }

  .#{$rkw-modal-selector}__wrapper {
    position: relative;
    height: 100%;
    padding-bottom: 10rem;
    overflow: auto;
  }

  .#{$rkw-modal-selector}__content {
    @extend .o-container;
    top: 4.5rem;
  }

  .#{$rkw-modal-selector}__nav-next {
    display: none!important;
  }
  .#{$rkw-modal-selector}__nav-prev {
    display: none!important;
  }
  .#{$rkw-modal-selector}__nav-zoom {
    display: none;
  }
  .#{$rkw-modal-selector}__nav-close {
    display: none;
  }

  .#{$rkw-modal-selector}__nav-zoomIn {
    position: absolute;
    width: 5rem;
    height: 5rem;
    top: 1rem;
    right: -5rem;
    transform: translatex(100%);
    background: transparent;
    &::after {
      content: "\e914";
      position: absolute;
      top: 0;
      right: 0;
      font: normal normal normal 3.5rem/5.5rem 'rkw-digikit';
      color: #ffffff;
    }
  }

  .c-rkw-content__small-wrap {
    display: none!important;
  }
  .c-rkw-content__big-wrap {
    display: block!important;
  }

  .#{$rkw-modal-selector}__bullet-nav {
    display: none;
  }

}

.#{$rkw-modal-selector}--onFlip {
  .#{$rkw-modal-selector}__flip--front {
    transform: rotateY(180deg);
    .c-rkw-content__buttons {
      display: none;
    }
  }
  .#{$rkw-modal-selector}__flip--back {
    transform: rotateX(0deg) rotateY(0deg);
    z-index: 1000;
  }
}

.#{$rkw-modal-selector}__flip {
  position: absolute;
  height: 100%;
  width: 100%;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  transform-origin: center center;
  transition: all ease 0.5s;
  transform: rotateX(0deg) rotateY(0deg);
}
.#{$rkw-modal-selector}__flip--front {
  z-index: 900;
  position: absolute;
  height: 100%;
  width: 100%;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  transform-origin: center center;
  transition: all ease 0.5s;
  transform: rotateX(0deg) rotateY(0deg);
  box-shadow: 0.3rem 0.3rem 0.5rem rgba(51, 51, 51, 0.65);
}
.#{$rkw-modal-selector}__flip--back {
  z-index: -1;
  position: absolute;
  height: 100%;
  width: 100%;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  transform-origin: center center;
  transition: all ease 0.5s;
  transform: rotateY(-180deg);
  box-shadow: 0.3rem 0.3rem 0.5rem rgba(51, 51, 51, 0.65);
}

.#{$rkw-modal-selector}__wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: background-color 0.5s ease;
  background-color: #ffffff;
  z-index: 50;
}

.#{$rkw-modal-selector}__wrapper--color-1 {
  background-color: $bm-components-rkw-modal-color-1!important; // var

}

.#{$rkw-modal-selector}__wrapper--color-2 {
  background-color: $bm-components-rkw-modal-color-2!important; // var
}

.#{$rkw-modal-selector}__wrapper--color-3 {
  background-color: $bm-components-rkw-modal-color-3!important; // var
}

.#{$rkw-modal-selector}__wrapper--color-4 {
  background-color: $bm-components-rkw-modal-color-4!important; // var
}

.#{$rkw-modal-selector}__content {
  position: absolute;
  height: 100%;
  top: 0;
  left: 0;
  color: #ffffff;
  padding: 0 9rem;
}

.#{$rkw-modal-selector}__bullet-nav {
  position: absolute;
  bottom: 3rem;
  left: 50%;
  transform: translatex(-50%);
  z-index: 100;
}

.#{$rkw-modal-selector}__bullet {
  display: inline-block;
  width: 1rem;
  height: 1rem;
  margin: 0 0.2rem;
  border-radius: 50%;
  background-color: #ffffff;
  cursor: pointer;

  &--active {
    background-color: transparent;
    border: 1px solid #ffffff;
  }

}

.#{$rkw-modal-selector}__nav-zoom {
  visibility: hidden!important;
  position: absolute;
  top: 3rem;
  right: 9rem;
  color: #ffffff;
  z-index: 100;
  &::after {
    position: absolute;
    bottom: auto;
    top: 0;
    right: 0;
    font: normal normal normal 2.6rem/3rem FontAwesome;
    content: "\f065";
    color: #ffffff;
  }
}

.#{$rkw-modal-selector}__nav-close {
  position: absolute;
  top: 3rem;
  right: 3rem;
  color: #ffffff;
  z-index: 100;
  &::after {
    content: "\e914";
    position: absolute;
    top: 0;
    right: 0;
    font: normal normal normal 3.5rem/5.5rem 'rkw-digikit';
    color: #ffffff;
  }
}
.#{$rkw-modal-selector}__nav-next {
  position: absolute;
  top: 50%;
  right: 2rem;
  color: #ffffff;
  z-index: 100;
  &::after {
    position: absolute;
    bottom: auto;
    top: 0;
    right: 0;
    font: normal normal normal 4rem/4rem 'rkw-digikit' !important;
    content: "\e901";
    color: #ffffff;
    transform: translatey(-50%);
  }
}
.#{$rkw-modal-selector}__nav-prev {
  position: absolute;
  top: 50%;
  left: 2rem;
  color: #ffffff;
  z-index: 100;
  &::after {
    position: absolute;
    bottom: auto;
    top: 0;
    left: 0;
    font: normal normal normal 4rem/4rem 'rkw-digikit' !important;
    content: "\e900";
    color: #ffffff;
    transform: translatey(-50%);
  }
}


