// Image Slider Component

// Options

$bm-components-rkw-slider : true;

$bm-components-rkw-slider-namespace : 'rkw-slider' !default;


// Helper Local Variables

$bm-components-rkw-slider-namespace-var-nav : $iota-global-components-namespace + $bm-components-rkw-slider-namespace;

$rkw-slider-selector: $bm-components-rkw-slider-namespace-var-nav;

.#{$rkw-slider-selector} {
  position: relative;
  width: 100%;
  padding-bottom: 50%;

  &:hover {
    .#{$rkw-slider-selector}__prev,
    .#{$rkw-slider-selector}__next {
      opacity: 0.5;
      &:hover {
        opacity: 1;
      }
    }
  }

}

.#{$rkw-slider-selector}__prev {
  position: absolute;
  top: 0;
  left: 0;
  width: 10%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.2s linear;
  cursor: pointer;
  &::after {
    position: absolute;
    bottom: auto;
    top: 50%;
    left: 3rem;
    font: normal normal normal 4rem/4rem 'rkw-digikit' !important;
    content: "\e900";
    color: #ffffff;
    transform: translatey(-50%);
  }
}

.#{$rkw-slider-selector}__next {
  position: absolute;
  top: 0;
  right: 0;
  width: 10%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.2s linear;
  cursor: pointer;
  &::after {
    position: absolute;
    bottom: auto;
    top: 50%;
    right: 3rem;
    font: normal normal normal 4rem/4rem 'rkw-digikit' !important;
    content: "\e901";
    color: #ffffff;
    transform: translatey(-50%);
  }
}

.#{$rkw-slider-selector}__nav {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 6rem;
  padding-top: 2rem;
  transform: translatey(100%);
  text-align: center;
  white-space: nowrap;
}

.#{$rkw-slider-selector}__bullet {
  display: inline-block;
  width: 1.5rem;
  height: 1.5rem;
  margin: 0 0.5rem;
  border-radius: 50%;
  background-color: #ffffff;
  cursor: pointer;
  &--active {
    background-color: transparent;
    border: 1px solid #ffffff;
  }
}

.#{$rkw-slider-selector}__item {
  position: absolute;
  width: 100%;
  padding-bottom: 50%;
}

.#{$rkw-slider-selector}__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url($path-assets-images + 'placeholder-image.png');
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  opacity: 0;
  transition: opacity 0.6s ease;

  &--active {
    opacity: 1;
  }

}

.#{$rkw-slider-selector}__image--mechanism-icon {
  background-image: url($path-assets-images + 'mechanism_icon.svg');
  background-size: contain;
}


