// Content Navigation Component




// Options

$bm-components-rkw-content : true;

$bm-components-rkw-content-namespace : 'rkw-content' !default;


// Helper Local Variables

$bm-components-rkw-content-namespace-var-nav : $iota-global-components-namespace + $bm-components-rkw-content-namespace;

$rkw-content-selector: $bm-components-rkw-content-namespace-var-nav;

.#{$rkw-content-selector} {
  width: 0;
  height: 0;
  overflow: hidden;
  visibility: hidden;
}

.#{$rkw-content-selector}__headerbox {
  min-height: 7rem;
  margin-bottom: 3rem;

  .#{$rkw-content-selector}__breadcrumb {
    top: auto;
    bottom: -0.4rem;
    left: 9rem;
  }

}

.#{$rkw-content-selector}__small-wrap {
  margin-top: 12rem;
}

.#{$rkw-content-selector}__big-wrap {

  .#{$rkw-content-selector}__breadcrumb-item {
    &:last-of-type {
      &::after {
        content: "";
        padding: 0;
      }
    }
  }

  .#{$rkw-content-selector}__slider {
    margin-bottom: 12rem;
  }

  .#{$rkw-content-selector}__button--unlike,
  .#{$rkw-content-selector}__button--like {
    float: right!important;
  }

}

.#{$rkw-content-selector}__breadcrumb {
  position: absolute;
  width: 60%;
  top: 3rem;
  font-size: 1.6rem;
  line-height: 2.4rem;
}

.#{$rkw-content-selector}__breadcrumb-item {

  font-family: "ff-tisa-sans-web-pro", sans-serif;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;


  &:last-of-type {
    &::after {
      content: ":";
      padding: 0;
    }
  }

  &::after {
    padding-left: 1rem;
    padding-right: 1rem;
    content: "\003E";
  }
}

.#{$rkw-content-selector}__oversize-container {
  margin-left: -10rem;
  margin-right: -10rem;
}

.#{$rkw-content-selector}__line {
  border: none;
  border-top: 0.2rem solid #ffffff;
  margin-top: 6rem;
  &--small {
    margin-top: 0;
    margin-bottom: 3rem;
  }
}

.#{$rkw-content-selector}__slider {
  margin-top: 0;
  margin-bottom: 3rem;
}

.#{$rkw-content-selector}__header {
  font-size: 2.8rem;
  line-height: 3.8rem;
  font-family: "ff-tisa-sans-web-pro", sans-serif;
  font-weight: 400;
  margin-bottom: 2.5rem;
  margin-top: -1rem;

  &--italic {
    font-style: italic;
  }

}

.#{$rkw-content-selector}__subheader {
  display: block;
  font-size: 2.4rem;
  line-height: 3.2rem;
  font-family: "ff-tisa-sans-web-pro", sans-serif;
  font-weight: 400;
  margin-bottom: 2.5rem;
  padding-right: 2.5rem;
}

.#{$rkw-content-selector}__header2 {
  font-size: 3.6rem;
  line-height: 3.6rem;
  font-family: "ff-tisa-sans-web-pro", sans-serif;
  font-weight: 500;
  margin-top: 6rem;
  margin-bottom: 6rem;
  text-align: center;
}

.#{$rkw-content-selector}__circle {
  background: #e1e1e1;
  position: relative;
  width: 100%;
  padding-bottom: 100%;
  box-shadow: 0.2rem 0.2rem 0.7rem #aaaaaa;
  cursor: pointer;
  border-radius: 50%;

  &::after {
    position: absolute;
    top: 50%;
    left: 50%;
    font: normal normal normal 3rem/3rem FontAwesome;
    content: "\f03e";
    transform: translate3d(-50%, -50%, 0);
    color: #575757;
  }
}

.#{$rkw-content-selector}__link {
  color: #ffffff;
  text-decoration: none;
}
.#{$rkw-content-selector}__ext-link {
  color: #ffffff;
  text-decoration: underline;
  &::before {
    font: normal normal normal 1.4rem/1.4rem 'rkw-digikit' !important;
    content: "\e911";
    margin-right: 0.8rem;
  }
}
.#{$rkw-content-selector}__download {
  color: #ffffff;
  text-decoration: none;
  &::before {
    font: normal normal normal 1.6rem/1.6rem 'rkw-digikit' !important;
    content: "\e904";
    margin-right: 0.8rem;
  }
}

.#{$rkw-content-selector}__text {
  display: block;
  text-align: center;
  margin-bottom: 2.5rem;
}

.#{$rkw-content-selector}__body {
  font-family: "ff-tisa-sans-web-pro", sans-serif;
  font-weight: 300;
  margin-bottom: 2.5rem;

  &--weight-invert {
    font-weight: 300!important;
    strong {
      font-weight: 600!important;
    }
  }


  .#{$rkw-content-selector}__text-light {
    font-weight: 300!important;
  }

  p {
    margin-bottom: 2.5rem;
  }

  ul {
    margin-left: 2.5rem;
    margin-bottom: 2.5rem;
  }

}

.#{$rkw-content-selector}__buttons {
  position: absolute;
  bottom: 7.5rem;
  width: 100%;
  left: 0;
  padding: 0 9rem;
  text-align: center;
  &--relative {
    position: relative;
    bottom: auto;
    padding: 0;
    left: auto;
  }
}

.#{$rkw-content-selector}__button {
  position: relative;
  display: inline-block;
  z-index: 100;
  width: 5rem;
  height: 5rem;
  background-color: transparent;
  border-radius: 5rem;
  cursor: pointer;
  margin-left: 3rem;
  &--invert {
    background-color: #ffffff;
    color: #000000;
    .c-rkw-modal__wrapper--color-1 & {
      &::after {
        color: $bm-components-rkw-modal-color-1!important;
      }
    }
    .c-rkw-modal__wrapper--color-2 & {
      &::after {
        color: $bm-components-rkw-modal-color-2!important;
      }
    }
    .c-rkw-modal__wrapper--color-3 & {
      &::after {
        color: $bm-components-rkw-modal-color-3!important;
      }
    }
    .c-rkw-modal__wrapper--color-4 & {
      &::after {
        color: $bm-components-rkw-modal-color-4!important;
      }
    }
  }
  &:last-child {
    margin-right: 0;
  }

  &--like {
    float: left;
    margin: 0;
    &::after {
      position: absolute;
      top: 50%;
      left: 50%;
      background: url($path-assets-images + 'empty_heart.svg') no-repeat center center;
      background-size: cover;
      content: " ";
      width: 100%;
      height: 100%;
      transform: translate3d(-50%, -50%,0);
    }
    &.whooshElement {
      &::after {
        background: url($path-assets-images + 'full_heart.svg') no-repeat center center;
      }
    }
  }
  &--unlike {
    float: left;
    margin: 0;
    &::after {
      position: absolute;
      top: 50%;
      left: 50%;
      background: url($path-assets-images + 'full_heart.svg') no-repeat center center;
      background-size: cover;
      content: " ";
      width: 100%;
      height: 100%;
      transform: translate3d(-50%, -50%,0);
    }
  }
  &--save {
    &::after {
      position: absolute;
      top: 50%;
      left: 50%;
      font: normal normal normal 3rem/3rem FontAwesome;
      content: "\f0f6";
      transform: translate3d(-50%, -50%, 0) rotate(180deg);
    }
  }
  &--flip {
    float: right;
    margin: 0;
    &::after {
      position: absolute;
      top: 50%;
      left: 50%;
      font: normal normal normal 5rem/5rem 'rkw-digikit' !important;
      content: "\e90b";
      transform: translate3d(-50%, -50%, 0);
    }
  }
  &--flip-back {
    float: right;
    margin: 0;
    &::after {
      position: absolute;
      top: 50%;
      left: 50%;
      font: normal normal normal 5rem/5rem 'rkw-digikit' !important;
      content: "\e903";
      transform: translate3d(-50%, -50%, 0);
    }
  }
  &--showmore {
    width: auto !important;
    line-height: 4.2rem;
    margin-left: 0 !important;
    border: 0.4rem solid #ffffff;
    &:after {
      font-size: 2rem;
      font-weight: 600;
      padding: 0 2rem 0 2rem;
      position: relative;
      content: 'Mehr dazu';
    }
  }
  &--skip,
  &--lets-go,
  &--tutorial,
  &--tutorial-prev,
  &--tutorial-next {
    width: auto !important;
    line-height: 4.2rem;
    border: 0.4rem solid #ffffff;
    margin-left: 0 !important;
    &:after {
      font-size: 2rem;
      font-weight: 600;
      padding: 0 2rem 0 2rem;
      position: relative;
      content: 'Skip';
    }
  }
  &--lets-go {
    margin-left: 3rem !important;
    &:after {
      content: 'Los gehts' !important
    }
  }
  &--tutorial {
    margin-left: 3rem !important;
    &:after {
      content: 'Tutorial starten' !important
    }
    &-prev {
      &+ .c-rkw-content__button--skip {
        margin-left: 3rem !important;
      }
      span {
        font-size: 2rem;
        font-weight: 600;
        padding: 0 2rem 0 2rem;
        position: relative;
      }
      &:after {
        padding: 0!important;
        content: '' !important
      }
    }
    &-next {
      margin-left: 3rem !important;
      &:after {
        content: 'Weiter' !important
      }
    }
  }

  &--bookmark-copy-url,
  &--bookmark-send-email {
    width: auto !important;
    font-weight: 600;
    line-height: 4.2rem;
    border: 0.4rem solid #575757;
    margin-left: 0 !important;
    padding: 0 2rem!important;
    color: #575757!important;
  }
}

.#{$rkw-content-selector}__videos {
  margin-bottom: 6rem;
}

.#{$rkw-content-selector}__video-item {
  position: relative;
  padding-bottom: 75%;
  background: #e1e1e1;
  cursor: pointer;
  display: block;
  color: #575757;
  &::after {
    position: absolute;
    top: 50%;
    left: 50%;
    font: normal normal normal 5rem/5rem 'rkw-digikit' !important;
    content: "\e90f";
    transform: translate3d(-50%, -50%, 0) scale(1.0);
    transition: transform 0.3s linear;
  }

  &:hover {
    &::after {
      transform: translate3d(-50%, -50%, 0) scale(1.2);
    }
  }
  + p {
    margin-top: 1rem;
    margin-bottom: 0;
  }
}

.#{$rkw-content-selector}__mechanism-img {
  width: 80%;
  height: auto;
}
.#{$rkw-content-selector}__mechanism-info {
  background-color: #ffffff;
  padding-top: 3rem;
  padding-bottom: 3rem;
  margin-bottom: 6rem;
}

.#{$rkw-content-selector}__share-item {
  display: inline-block;
  width: 5rem;
  height: 5rem;
  &::after {
    font: normal normal normal 5rem/5rem 'rkw-digikit' !important;
  }
  &--facebook {
    &::after {
      content: "\e906";
    }
  }
  &--twitter {
    &::after {
      content: "\e912";
    }
  }
  &--linkedin {
    &::after {
      content: "\e90d";
    }
  }
  &--email {
    &::after {
      content: "\e905";
    }
  }
  &--xing {
    text-align: center;
    &::after {
      content: "\f169";
    }
  }
}

.#{$rkw-content-selector}__contact {
  height: 100%;
  width: 100%;
  background-color: rgba(255,255,255, 0.75);
  padding: 3rem;
}

.#{$rkw-content-selector}__contact-header {
  min-height: 6rem;
  background: url($path-assets-images + 'logo_claim.png') no-repeat;
  background-size: 24.4rem 4.4rem!important;

  &--short {
    background: url($path-assets-images + 'logo_claim_short.png') no-repeat;
  }

}

.#{$rkw-content-selector}__contact-selection {
  font-size: 1.6rem;
  line-height: 3rem;
  color: #6c6c6c;
  border-bottom: 1px solid #6c6c6c;
  border-top: 1px solid #6c6c6c;
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
  margin-bottom: 1.5rem;
}

.#{$rkw-content-selector}__contact-person {
  color: #6c6c6c;
}


