// Content Navigation Component




// Options

$bm-components-tile-nav : true;
$bm-components-tile-nav-height : 97rem;

$bm-components-tile-nav-namespace : 'tile-nav' !default;


// Helper Local Variables

$bm-components-tile-nav-namespace-var-nav : $iota-global-components-namespace + $bm-components-tile-nav-namespace;

.#{$bm-components-tile-nav-namespace-var-nav} {
  height: $bm-components-tile-nav-height;
  position: relative;
}

.#{$bm-components-tile-nav-namespace-var-nav}__column {
  position: relative;
  z-index: 50;
}

.#{$bm-components-tile-nav-namespace-var-nav}__column--active {
  z-index: 500!important;
}

.#{$bm-components-tile-nav-namespace-var-nav}__column--order-1 {
  order: 1;
  z-index: 100;
}
.#{$bm-components-tile-nav-namespace-var-nav}__column--order-2 {
  order: 2;
  z-index: 200;
}
.#{$bm-components-tile-nav-namespace-var-nav}__column--order-3 {
  order: 3;
  z-index: 300;
}
.#{$bm-components-tile-nav-namespace-var-nav}__column--order-4 {
  order: 4;
  z-index: 400;
}

.#{$bm-components-tile-nav-namespace-var-nav}__wrapper {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  list-style: none;
}

.#{$bm-components-tile-nav-namespace-var-nav}__tile {
  position: absolute;
  display: block;
  width: 18.5rem;
  height: 18.5rem;
  padding: 1.5rem;
  font-family: "ff-tisa-sans-web-pro", sans-serif;
  @extend .u-color-rkw-white;
  @include iota-type(2.4rem);
  text-decoration: none;
  opacity: 1;
  transition: opacity 0.5s linear 0.2s;
  z-index: 100;
  box-shadow: 0.3rem 0.3rem 0.5rem rgba(51, 51, 51, 0.65);

}

.#{$bm-components-tile-nav-namespace-var-nav}__link-text {
  position: absolute;
  top: 1.5rem;
  bottom: auto;
  left: 1.5rem;
  right: auto;
}

.#{$bm-components-tile-nav-namespace-var-nav}__tile--active {
  opacity: 1;
}

.#{$bm-components-tile-nav-namespace-var-nav}__tile--inactive {
  opacity: 0.5;
}

.#{$bm-components-tile-nav-namespace-var-nav}__tile--category-1,
.#{$bm-components-tile-nav-namespace-var-nav}__tile-sub--category-1,
.#{$bm-components-tile-nav-namespace-var-nav}__tile-sub2--category-1 {
  @extend .u-bg-rkw-color-1;
}

.#{$bm-components-tile-nav-namespace-var-nav}__tile--category-2,
.#{$bm-components-tile-nav-namespace-var-nav}__tile-sub--category-2,
.#{$bm-components-tile-nav-namespace-var-nav}__tile-sub2--category-2 {
  @extend .u-bg-rkw-color-2;
}

.#{$bm-components-tile-nav-namespace-var-nav}__tile--category-3,
.#{$bm-components-tile-nav-namespace-var-nav}__tile-sub--category-3,
.#{$bm-components-tile-nav-namespace-var-nav}__tile-sub2--category-3 {
  @extend .u-bg-rkw-color-3;
}

.#{$bm-components-tile-nav-namespace-var-nav}__tile--category-4,
.#{$bm-components-tile-nav-namespace-var-nav}__tile-sub--category-4,
.#{$bm-components-tile-nav-namespace-var-nav}__tile-sub2--category-4 {
  @extend .u-bg-rkw-color-4;
}

.#{$bm-components-tile-nav-namespace-var-nav}__tile--top-right {
  bottom: 0!important;
  left: 0!important;
  transform: translateX(-1.5rem)!important;
  text-align: right;

  .#{$bm-components-tile-nav-namespace-var-nav}__link-text {
    left: auto;
    right: 1.5rem;
  }

}

.#{$bm-components-tile-nav-namespace-var-nav}__tile--top-left {
  bottom: 0!important;
  right: 0!important;
  transform: translateX(1.5rem)!important;
}

.#{$bm-components-tile-nav-namespace-var-nav}__tile--bottom-right {
  top: 0!important;
  left: 0!important;
  transform: translateX(-1.5rem)!important;
  text-align: right;

  .#{$bm-components-tile-nav-namespace-var-nav}__link-text {
    top: auto;
    bottom: 1.5rem;
    right: 1.5rem;
    left: auto;
  }
}

.#{$bm-components-tile-nav-namespace-var-nav}__tile--bottom-left {
  top: 0!important;
  right: 0!important;
  transform: translateX(1.5rem)!important;

  .#{$bm-components-tile-nav-namespace-var-nav}__link-text {
    top: auto;
    bottom: 1.5rem;
  }
}

// Sub-Level

.#{$bm-components-tile-nav-namespace-var-nav}__wrapper-sub {
  list-style: none;
  position: absolute;
}

.#{$bm-components-tile-nav-namespace-var-nav}__tile--top-left + .#{$bm-components-tile-nav-namespace-var-nav}__wrapper-sub {
