}

.#{$bm-components-tile-nav-namespace-var-nav}__tile + .#{$bm-components-tile-nav-namespace-var-nav}__wrapper-sub .#{$bm-components-tile-nav-namespace-var-nav}__tile-sub {
  position: absolute;
  display: block;
  width: 7.0rem;
  height: 7.0rem;
  font-family: "ff-tisa-sans-web-pro", sans-serif;
  @extend .u-color-rkw-white;
  @include iota-type(0.5rem, 1.0rem);
  text-decoration: none;
  opacity: 0.3;
  transition:
          opacity 0.3s linear 0.1s,
          width 0.4s ease 0.1s,
          height 0.4s ease 0.1s,
          top 0.5s ease 0s,
          bottom 0.5s ease 0s,
          left 0.5s ease 0s,
          right 0.5s ease 0s,
          font-size 0.4s linear 0.1s,
          line-height 0.4s linear 0.1s,
          box-shadow 0.5s linear 0.2s,
          transform 0.5s ease 0s;
  box-shadow: none;

  .#{$bm-components-tile-nav-namespace-var-nav}__link-text {
    visibility: hidden;
    transition: opacity 0.8s linear 0.2s;
    opacity: 0;
  }

}

.#{$bm-components-tile-nav-namespace-var-nav}__tile--active + .#{$bm-components-tile-nav-namespace-var-nav}__wrapper-sub .#{$bm-components-tile-nav-namespace-var-nav}__tile-sub {
  width: 17.0rem!important;
  height: 17.0rem!important;
  opacity: 0.85;
  box-shadow: 0.3rem 0.3rem 0.5rem rgba(51, 51, 51, 0.65);
  @include iota-type(1.8rem, 2.7rem);
  .#{$bm-components-tile-nav-namespace-var-nav}__link-text {
    display: block;
    visibility: visible;
    opacity: 1;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    text-align: center;
    padding: 1.5rem;
  }

  &:hover {
    opacity: 1;
  }

}

.#{$bm-components-tile-nav-namespace-var-nav}__tile--active + .#{$bm-components-tile-nav-namespace-var-nav}__wrapper-sub .#{$bm-components-tile-nav-namespace-var-nav}__tile-sub--active {
  &:hover {
    opacity: 0.85;
  }
}

.#{$bm-components-tile-nav-namespace-var-nav}__tile--active + .#{$bm-components-tile-nav-namespace-var-nav}__wrapper-sub .#{$bm-components-tile-nav-namespace-var-nav}__tile-sub--inactive {
  opacity: 0.5;
  &:hover {
    opacity: 0.85;
  }
}

.#{$bm-components-tile-nav-namespace-var-nav}__tile--inactive + .#{$bm-components-tile-nav-namespace-var-nav}__wrapper-sub .#{$bm-components-tile-nav-namespace-var-nav}__tile-sub {
