  @include iota-breakpoint(xxs) {
    font-size: 45.5%;
  }
  @include iota-breakpoint(xs) {
    font-size: 47.3%;
  }
  @include iota-breakpoint(sm) {
    font-size: 50%;
  }
  @include iota-breakpoint(md) {
    font-size: 52.7%;
  }
  @include iota-breakpoint(lg) {
    font-size: 55.4%;
  }
  @include iota-breakpoint(xl) {
    font-size: 58.1%;
  }
  @include iota-breakpoint(xxl) {
    font-size: 60.8%;
  }
  @include iota-breakpoint(xxxl) {
    font-size: 62.5%;
  }
  @include iota-breakpoint(axl) {
    font-size: 63.4%;
  }
  @include iota-breakpoint(bxl) {
    font-size: 66%;
