body, html {
  min-height: 100%;
}

html {
  height: 100%;
  position: relative;
}

body {
  padding-top: 4.5rem;
  padding-bottom: 4.5rem;
  font-family: "ff-tisa-sans-web-pro", sans-serif;
  font-size: 1.8rem;
  line-height: 2.6rem;
  -webkit-font-smoothing: antialiased;
  overflow: hidden;
}

a {
  text-decoration: none;
}

#rkwDigiKit {
  opacity: 0;
  transition: opacity 0.5s 0.8s ease-in;
  &.start {
    opacity: 1;
  }
}

.o-container {
  //border: 1px solid #e64415;
  position: relative;
}

.logo {
  position: absolute;
  width: 7rem;
  height: 7rem;
  top: 0;
  left: -3rem;
  transform: translatex(-100%);
  background: url($path-assets-images + 'rkw-logo.svg') no-repeat;
  background-size: cover;
}

.bookmarks {
  position: absolute;
  width: 5rem;
  height: 5rem;
  top: 0;
  right: -5rem;
  transform: translatex(100%);
  background: url($path-assets-images + 'folder-heart.svg') no-repeat center center;
  background-size: cover;
  &::after {
    position: absolute;
    top: 50%;
    left: 50%;
    font: normal normal normal 5rem/5rem 'rkw-digikit' !important;
    //content: "\e907";
    transform: translate3d(-50%, -50%, 0);
    color: #575757;
  }
}

.c-rkw-tutorial {
  position: absolute;
  width: 5rem;
  height: 5rem;
  top: 7rem;
  right: -5rem;
  transform: translatex(100%);
  background: transparent;
  &::after {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    text-align: center;
    font: normal normal normal 3rem/3rem 'rkw-digikit' !important;
    content: "\e913";
    transform: translate3d(-50%, -50%, 0);
    color: #575757;
  }
}

.c-rkw-customer {
  position: absolute;
  top: 50%;
  left: 50%;
  right: auto;
  width: 10rem;
  height: 10rem;
  margin-left: 0;
  transform: translate3d(-50%,-50%,0);
  transform-origin: center center;
  z-index: 1000;
  background-color: #ffffff;
  font-family: "ff-tisa-sans-web-pro",sans-serif;
  color: #575757;
  @include iota-type(2.4rem);
  line-height: 10rem;
  text-align: center;
}


.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}

.whooshElement {
  position: fixed!important;
  opacity: 1;
  transition: opacity 1s ease, left 1s ease, top 1s ease;
  z-index: 8000;
  color: #ffffff;
}


