// Application Tour Component

// Options
$bm-components-application-tour: true;

$bm-components-application-tour-namespace: 'application-tour' !default;

// Helper Local Variables
$bm-components-application-tour-namespace-var: $iota-global-components-namespace + $bm-components-application-tour-namespace;

$application-tour-selector: $bm-components-application-tour-namespace-var;

.#{$application-tour-selector} {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 1010;
  opacity: 1;
  background-color: rgba(0,0,0,.8);
  &__container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
  }
  &__close {
    cursor: pointer;
    position: absolute;
    width: 5rem;
    height: 5rem;
    top: 4.5rem;
    right: 3rem;
    background: transparent;
    z-index: 1020;
    &::after {
      font: normal normal normal 3.5rem/5rem 'rkw-digikit';
      content: "\e914";
      color: #ffffff;
    }
  }
  &__content {
    max-width: 119.6rem;
    color: #ffffff;
    text-align: center;
    .c-application-tour__video,
    .c-application-tour__image {
      width: 100%;
      max-height: 60vh;
      margin-bottom: 2rem;
    }
  }
  &__tutorial-control {
    width: 100%;
    margin-top: 2rem;
  }
}

.c-rkw-content__header--application-tour {
  font-size: 3.2rem!important;
  font-weight: 600!important;
}

.c-rkw-content__body--application-tour {
  margin-left: auto;
  margin-right: auto;
  max-width: 50%!important;
  margin-bottom: 6rem!important;
  font-size: 2.2rem!important;
  line-height: 2.9rem!important;
}

.c-rkw-content__body--application-tour-description {
  width: auto!important;
  margin: 0 auto!important;
  font-size: 2.2rem!important;
  line-height: 2.9rem!important;
  margin-bottom: 4rem!important;
  padding: 0 3rem!important;
}

.application-tour__fade-welcome,
.application-tour__fade,
.application-tour__fade-content {
  transition: opacity .5s ease-in-out;
  &-enter-active,
  &-leave-active {
    transition: opacity .5s ease-in-out;
  }
}

.application-tour__fade {
  &-enter {
    opacity: 0;
    &-to {
      opacity: 1;
    }
  }
  &-leave {
    opacity: 1;
    &-to {
      opacity: 0;
    }
  }
}

.application-tour__fade-content {
  &-enter {
    opacity: 0;
    &-to {
      opacity: 1;
    }
  }
  &-leave {
    opacity: 1;
    &-to {
      opacity: 0;
    }
  }
}


