@font-face {
  font-family: 'rkw-digikit';
  src:  url($path-assets-fonts + 'rkw-digikit.eot?olgsy');
  src:  url($path-assets-fonts + 'rkw-digikit.eot?olgsy#iefix') format('embedded-opentype'),
  url($path-assets-fonts + 'rkw-digikit.ttf?olgsy') format('truetype'),
  url($path-assets-fonts + 'rkw-digikit.woff?olgsy') format('woff'),
  url($path-assets-fonts + 'rkw-digikit.svg?olgsy#rkw-digikit') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'rkw-digikit' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

